body {
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
}

img {
  width: 100%;
}

.mt-70 {
  margin-top: 70px;
}

.mb-60 {
  margin-bottom: 60px;
}

.mb-30 {
  margin-bottom: 40px;
}

UL {
  padding: 0;
  margin: 0;
}

p {
  padding: 0;
}

.pro_content p {
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  font-size: 18px;
  margin: 10px 0;
}

.pro_content span {
  font-weight: 900;
}

.section-title {
  margin-bottom: 30px;
}

.section-title h2 {
  text-transform: uppercase;
  margin: 40px 0;
  font-weight: 900;
}

.bannar-wrapper {
  min-height: 500px;
  background-image: url("https://cdn.shopify.com/s/files/1/0035/5935/8509/files/car-slider1_3000x.jpg?v=1605861483");
  background-size: cover;
}

.bannar-wrapper span {
  font-size: 18px;
  margin-bottom: 10px;
  color: #ddd;
}

.bannar-wrapper h2 {
  font-size: 60px;
  font-weight: 900;
  margin: 15px 0;
  color: #fff;
}

.bannar-wrapper a.btn {
  color: #fff;
  background: #f41d18;
  padding: 8px 25px;
}

.bannar-offer {
  margin: 30px 0;
}

.collection-cta {
  padding: 75px 0;
  background: #ddd;
  transition: all .3s ease-in-out;
  background: #0E0F2C;
}

.collection-cta .catalog {
  position: relative;
  overflow: hidden;
  transition: 0.3s all ease-in-out;
}

.collection-cta .catalog::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.5;
  transition: 0.3s all ease-in-out;
}

.collection-cta .catalog .cta-content {
  color: #fff;
  position: absolute;
  text-align: center;
  width: 100%;
  top: 50%;
  padding: 0 5px;
  -ms-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  transition: .2s;
  hyphens: auto;
  z-index: 10;
  -webkit-transition: .3s;
  -moz-transition: .3s;
  -o-transition: .3s;
}

.collection-cta .catalog .cta-content h6 {
  font-size: 28px;
  font-weight: bold;
  transition: 0.3s all ease-in-out;
  margin: 0;
}

.collection-cta .catalog .cta-content span {
  font-size: 18px;
}

.cta-img {
  font-size: 20px;
}

.cta-img img {
  transition: transform .5s ease;
}

.catalog:hover .cta-img > img {
  transform: scale(1.11);
}

.catalog:hover::before {
  opacity: 0.5;
}

.catalog:hover h6 {
  transform: scale(1.11);
}

.offer-section {
  padding: 60px 0;
  background: #0E0F2C;
}

.offer-section .featured-wrapper {
  margin-bottom: 30px;
}

.offer-section .featured-wrapper .featued-icon i {
  font-size: 40px;
  color: #fff;
}

.offer-section .featured-wrapper .featued-text {
  margin-left: 15px;
}

.offer-section .featured-wrapper .featued-text h5 {
  font-size: 26px;
  margin: 0 0 10px 0;
  color: #fff;
}

.offer-section .featured-wrapper .featued-text p {
  color: #fff;
}

.product_details_img {
  margin: 20px 0;
}

.product_details_info {
  margin-left: 60px;
}

.product_details_info span {
  color: #60d47e;
}

.product_details_info h3 {
  font-size: 28px;
}

.pro_price_add {
  z-index: 15;
  background: #000;
}

.pro_price_add a {
  color: #fff;
  display: inline-block;
  font-weight: bold;
  font-family: 'Raleway', sans-serif;
  width: 63%;
}

.pro_price_add a:focus {
  box-shadow: none;
}

.product-counter {
  display: inline-block;
}

.product-counter input#quantity {
  background: none;
  color: #fff;
  width: 30px;
  outline: none;
  border: 0px;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  padding: 0px;
}

.product-counter input#quantity:focus {
  padding: 0;
}

.prduct-grid .pro_img {
  position: relative;
  overflow: hidden;
}

.prduct-grid .discount {
  left: 11%;
  top: 4%;
  position: absolute;
}

.prduct-grid .discount span {
  padding: 2px 7px;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

.prduct-grid .discount span.out {
  background: #0E0F2C;
}

.prduct-grid .discount span.offer {
  background: green;
}

.prduct-grid .pro_img_2nd {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  top: 0;
  opacity: 0;
  transition: 1s;
}

.prduct-grid:hover .pro_img_2nd {
  opacity: 1;
  transform: scale(1.09);
}

.collection-02 {
  padding: 60px 0 100px 0;
  background: #000;
}

.collection-02 h2 {
  color: #fff;
  margin-bottom: 40px;
}

.collection-02 .iamg {
  transition: 0.3s;
}

.collection-02 .singal-cat-02 {
  position: relative;
  overflow: hidden;
  transition: 0.4s;
}

.collection-02 .singal-cat-02::before {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: .4;
  content: "";
}

.collection-02 .singal-cat-02 .cat02-text {
  position: absolute;
  width: 100%;
  bottom: 50%;
  margin-bottom: -19px;
  text-align: center;
}

.collection-02 .singal-cat-02 .cat02-text h6 {
  color: #fff;
  font-weight: 400;
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  transition: 0.3s all ease-in-out;
}

.collection-02 .singal-cat-02:hover .cat02-text h6 {
  transform: scale(1.11);
}

.collection-02 .singal-cat-02:hover .iamg {
  transform: scale(1.09);
}

.collection-02 .singal-cat-02:hover::before {
  opacity: .6;
}

.grid-item {
  margin: 10px 0;
}

.p_plus {
  width: 30px;
  height: 30px;
  border: none;
  position: relative;
  display: inline-block;
  padding: 0;
  margin: 0;
}

.p_plus:before, .p_plus:after {
  content: "";
  position: absolute;
  top: 74%;
  left: 40%;
  width: 18px;
  height: 2px;
  transform: translate(-50%, -50%);
  background: #fff;
  z-index: 10;
  pointer-events: none;
  cursor: default;
}

.p_plus:before {
  transform: translate(-50%, -50%) rotate(90deg);
}

.p_plus #plus {
  background: transparent;
  border: none;
  width: 20px;
}

.p_minus {
  width: 30px;
  height: 30px;
  border: none;
  position: relative;
  display: inline-block;
  padding: 0;
  margin: 0;
}

.p_minus:before, .p_minus:after {
  content: "";
  position: absolute;
  top: 74%;
  left: 41%;
  width: 18px;
  height: 2px;
  transform: translate(-50%, -50%);
  background: #fff;
  z-index: 10;
  pointer-events: none;
  cursor: default;
}

.p_minus #minus {
  background: transparent;
  border: none;
  width: 22px;
}

